<template>
  <div class="page trialpreson flex-v medicalrecord">
    <div class="headertitle flex">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb ">
        <el-breadcrumb-item :to="{name:'ReportForm'}">报表分布图</el-breadcrumb-item>
        <el-breadcrumb-item :to="{name:'ReportForm'}">体检记录-{{texttitle}}</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="flex rightbtn">
        <div class="marginright50"
          @click="exportFile"
          style="font-size:16px">
          <span>
            <img src="../../assets/img/dsochuIcon.png" />
          </span>&nbsp;导出
        </div>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        :model="queryObj"
        class="default-form-style"
        size='small'>
        <el-form-item label="登记日期：">
          <el-date-picker value-format='yyyy-MM-dd'
            v-model="dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label="机构名称：">
          <el-select v-model="queryObj.createOrganizationId"
            placeholder="全部"
            clearable>
            <el-option :label="item.oname"
              :value="item.sysGuid"
              v-for="(item,index) in orgList"
              :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="类别：">
          <el-select v-model="queryObj.personType"
            placeholder="全部"
            clearable>
            <el-option label="全部"
              value=""></el-option>
            <el-option label="普通居民"
              value="1"></el-option>
            <el-option label="儿童"
              value="2"></el-option>
            <el-option label="老年人"
              value="4"></el-option>
            <el-option label="孕妇 "
              value="8"></el-option>
            <el-option label="高血压"
              value="16"></el-option>
            <el-option label="肺结核"
              value="32"></el-option>
            <el-option label="糖尿病 "
              value="64"></el-option>
            <el-option label="精神病"
              value="128"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="searchbtn "
            icon="el-icon-search"
            round
            @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tabledata padding20 flex-v"
      style="padding:5px 20px 20px 20px">
      <div class="countlist flex-justify">
        <template v-for="(item,index) in countlist">
          <div class="countlist-item flex flex-item"
            :key="index"
            v-if="item.countshow">
            <div>
              <div class=" itemcount"
                :class="`sizecolor${index}`">{{item.count}}</div>
              <div class="itemtitle">{{item.title}}</div>
            </div>
            <div>
              <span class="icon iconfont"
                :class="`bj-${item.icon}`"></span>
            </div>
          </div>
        </template>
      </div>
      <div class="table-box "
        style="padding:0;overflow:auto;flex: 1;">
        <el-table stripe
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          height='100%'
          v-loading="loading"
          header-cell-class-name='custom-table-cell'>
          <el-table-column type="index"
            width="50"
            label="序号">
            <template v-slot='scope'>
              {{scope.$index + (pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column align="center"
            label="个人登记流水号"
            prop="sysGuid"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column align="center"
            prop="userName"
            label="姓名">
          </el-table-column>
          <el-table-column align="center"
            label="性别"
            width="80">
            <template slot-scope="scope">
              <div v-if="scope.row.sexType==1">男</div>
              <div v-else-if="scope.row.sexType==2">女</div>
              <div v-else>不详</div>
            </template>
          </el-table-column>
          <el-table-column align="center"
            label="年龄"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div>{{scope.row.age | emptyData}}</div>
            </template>
          </el-table-column>
          <el-table-column align="center"
            prop="examNum"
            label="体检次数"
            width="80">
          </el-table-column>
          <el-table-column align="center"
            label="联系电话"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div>{{scope.row.contactPhone | emptyData}}</div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="queryObj"
        query-url="/charts/base/findExamRecordList"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="controlLoading" />
    </div>
  </div>
</template>
<script>
import { xhrDownload } from '@/lib/xhr'
import { formatDate, exportFile } from '@/lib/utils'
export default {
  name: 'medicalrecord',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    let nowDate = formatDate(new Date())
    return {
      texttitle: '',
      loading: false,
      dateRange: [nowDate, `${nowDate} 23:59:59`],
      OrganizationId: '',
      queryObj: {
        startCreateDate: nowDate,
        endCreateDate: `${nowDate} 23:59:59`,
        personType: '',
        createOrganizationId,
        examStatus: '',
        sexType: ''
      },
      pageNum: 1,
      pageSize: 10,
      pageindex: '',
      countlist: [
        { title: '今日总人数（人）', count: '', icon: 'ponsercount', countshow: false },
        { title: '今日男性人数（人）', count: '', icon: 'manIcon', countshow: false },
        { title: '今日女性人数（人）', count: '', icon: 'womenIcon', countshow: false },
        { title: '今日已检人数（人）', count: '', icon: 'yijiancount', countshow: false },
        { title: '今日未检人数（人）', count: '', icon: 'weijiancount', countshow: false }
      ],
      tableData: []
    }
  },
  created() {
    this.init()
    this.queryObj.sexType = this.$route.query.sexType
    this.queryObj.examStatus = this.$route.query.examStatus
    if (this.queryObj.sexType === '1' || this.queryObj.sexType === '2') {
      this.countlist[0].countshow = true
      this.countlist[3].countshow = true
      this.countlist[4].countshow = true
      if (this.queryObj.sexType === '1') {
        this.texttitle = '男性'
      } else {
        this.texttitle = '女性'
      }
    } else if (this.queryObj.examStatus === 'succ' || this.queryObj.examStatus === 'notSucc') {
      this.countlist[0].countshow = true
      this.countlist[1].countshow = true
      this.countlist[2].countshow = true
      if (this.queryObj.examStatus === 'succ') {
        this.texttitle = '已体检'
      } else {
        this.texttitle = '未体检'
      }
    } else {
      this.countlist[0].countshow = true
      this.countlist[1].countshow = true
      this.countlist[2].countshow = true
      this.countlist[3].countshow = true
      this.countlist[4].countshow = true
      this.texttitle = '总体检'
    }
  },

  methods: {
    init() {
      let startCreateDate = formatDate(new Date())
      let endCreateDate = `${startCreateDate} 23:59:59`
      let { examStatus, sexType } = this.$route.query
      this.exportFileParams = {
        examStatus,
        sexType,
        startCreateDate,
        endCreateDate,
        createOrganizationId: this.queryObj.createOrganizationId
      }
      this.chartstatistic()
    },
    exportFile() {
      if (!this.tableData.length) return this.$message.warning('没有可用数据可导出！')
      this.$confirm('', {
        title: '确定导出？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          xhrDownload('/charts/base/exportExamRecord', this.exportFileParams, 'arraybuffer')
            .then(({ data }) => {
              let fileName = `体检记录-${this.texttitle}`
              exportFile(data, fileName)
            })
            .catch(err => this.$message.error(err))
        })
        .catch(cancel => cancel)
    },

    //图表统计
    chartstatistic(index) {
      let url = '/charts/base/getOrgRegisterPersonsNum'
      let params = { createOrganizationId: this.queryObj.createOrganizationId }
      this.$post(url, params)
        .then(res => {
          console.log('res', res)
          this.countlist[0].count = res.data.allPersion
          this.countlist[1].count = res.data.malePersion
          this.countlist[2].count = res.data.femalePersion
          this.countlist[3].count = res.data.todayExamSuccPersion
          this.countlist[4].count = res.data.regNotExamPersion
        })
        .catch(error => {
          this.$message.error(error)
        })
    },
    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
      let { startCreateDate, endCreateDate, personType, createOrganizationId } = this.queryObj
      Object.assign(this.exportFileParams, {
        startCreateDate,
        endCreateDate,
        personType,
        createOrganizationId,
        pageNum,
        pageSize
      })
    },
    controlLoading(loading) {
      this.loading = loading
    },
    onSubmit() {
      this.chartstatistic()
      this.$refs.paginationEvent.initPageList(1)
    }
  },
  computed: {
    orgList() {
      return this.$store.state.orgList
    }
  },
  watch: {
    dateRange(val) {
      if (val) {
        this.queryObj.startCreateDate = val[0]
        this.queryObj.endCreateDate = `${val[1]} 23:59:59`
      } else {
        this.queryObj.startCreateDate = ''
        this.queryObj.endCreateDate = ''
      }
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.medicalrecord {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .rightbtn {
    display: flex;
    .marginright50 {
      margin-right: 50px;
    }
    div {
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #666666;
      span {
        width: 25px;
        height: 60px;
      }
    }
  }
  .countlist {
    margin-bottom: 20px;
    .countlist-item {
      padding: 0 30px;
      margin-left: 20px;
      justify-content: space-between;
      flex-direction: row;
      align-items: center;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      &:first-child {
        margin-left: 0;
      }
      .iconfont {
        font-size: 50px;
      }
      .itemtitle {
        font-size: 14px;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
      }
      .sizecolor0 {
        color: #07caa6;
      }
      .sizecolor1 {
        color: #70cafe;
      }
      .sizecolor2 {
        color: #fe8470;
      }
      .sizecolor3 {
        color: #15dbd4;
      }
      .sizecolor4 {
        color: #ffc000;
      }
      .bj-ponsercount {
        color: #07caa6;
      }
      .bj-manIcon {
        color: #70cafe;
      }
      .bj-womenIcon {
        color: #fe8470;
      }
      .bj-yijiancount {
        color: #15dbd4;
      }
      .bj-weijiancount {
        color: #ffc000;
      }
    }
  }
}
</style>

